<template>
  <div class="hello">
    <h1>{{ msg }}</h1>

    <!-- actualiser liste d'images-->
    <button class="Search__button" @click="callRestService()">Call Spring Boot REST backend</button>
    <br/><br/>

    <!-- liste des images avec atualisation dans img-->
    <div>
      <select v-model="selected">
        <option v-for="(name, id) in response" :key="id" v-on:click="updateImage(id)"> {{ name }} </option>
      </select>
      <br/><br/>

      <!-- ajouter une image dans le serveur-->
      <div class="container">
        <div class="large-12 medium-12 small-12 cell">
          <label> Upload a file <br><br>
            <input type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
          </label>
            <button v-on:click="submitFile();callRestService()">Submit</button>

        </div>
      </div>

      <br/><br/>

    </div>

    <br/><br/>

    <!-- apperçu de l'image -->
      <div class="slider">
        <button @click="prev" class="btn btn-left">
            <img src="../assets/chevron_left.svg" class="icon">
        </button>
        <div class="container-slides" v-bind:style="{transform: `translateX(${index}px)`, transition}">
            <img id="currentImage" src="" class="img-slider">
            <img id="currentImage" src="" class="img-slider">
        </div>
        <button @click="next" class="btn btn-right">
            <img src="../assets/chevron_right.svg" class="icon">
        </button>
         <img id="currentImage" src="" class="img-slider">
    </div>

   <!-- <img id ="currentImage" src="">-->
   <div>
    <input v-model="message" placeholder="un nombre">
    <select v-model="selected">
      <option v-for="(name, id) in response" :key="name" > {{ id }} </option>
    </select>
      <button @click="lumButton(selected,message)"> Apply filter</button>
      <img id="currentImage" src="">
      <button @click="download(selected)"> Download Image </button>
   </div>

   <br><br>

   <!-- effacer une image de la liste-->

   <button v-on:click="erraseImage(selected);callRestService()"> Delete </button>

    <br><br>

    <div id="app">
    </div>
    
    <br><br><br>
  </div>

</template>


<script>
//import Slider from './Slider'
import axios from "axios";
export default {
  name: "HelloWorld",
  
 props: {
    msg: String,
  },
  data() {
    return {
      response: "[]",
      errors: [],
      selected: '',
      files:'',
      index: 0,
      transition: "transform 0,2s ease"
    };
  },
  mounted(){
    this.callRestService();
  },

  methods: {
    callRestService() {
      axios
        .get(`images`)
        .then((response) => {
          // JSON responses are automatically parsed.
          this.response = response.data;
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },

    updateImage(id){
      const imageUrl = "/images/" + id;
      const imageEl = document.getElementById('currentImage');
      axios.get(imageUrl, { responseType:"blob" }).then(function (response) {
        const reader = new window.FileReader();
        reader.readAsDataURL(response.data); 
        reader.onload = function() {
        const imageDataUrl = reader.result;
        imageEl.setAttribute('src', imageDataUrl);
        }
      })
    },

    updateImage2(id,x){
      const imageUrl = "/images/a" + id+"/" + x;
      const imageEl = document.getElementById('currentImage');

      axios.get(imageUrl, { responseType:"blob" }).then(function (response) {

        const reader = new window.FileReader();
        reader.readAsDataURL(response.data); 
        reader.onload = function() {
        const imageDataUrl = reader.result;
        imageEl.setAttribute('src', imageDataUrl);
        }
      })
    },

    lumButton(id,x){
      const imageUrl = "/images/a" + id+"/" +x;
      const imageEl = document.getElementById('currentImage');
      axios.get(imageUrl,{responseType:"blob"}).then(function(response){

        const reader = new window.FileReader();
        reader.readAsDataURL(response.data); 
        reader.onload = function() {
        const imageDataUrl = reader.result;
        imageEl.setAttribute('src', imageDataUrl);
        }
      })

    },

    download(id){
      axios({
        url: "/images/" + id ,
        method: 'GET',
        responseType:'blob'
      }).then((response) => {
        var fileUrl = window.URL.createObjectURL(new Blob([response.data]))
        var fileLink = document.createElement('a')
        fileLink.href = fileUrl
        fileLink.setAttribute('download', response.data.name)
        document.body.appendChild(fileLink)
        fileLink.click()
      })
    },

    submitFile(){
      let formData = new FormData();
      formData.append('file', this.file);
      axios.post('/images/', formData, {
        headers: { 'Content-Type': 'multipart/form-data'}
      }).then(function(){
        console.log('SUCCESS!!');
      })
      .catch(function(){
        console.log('FAILURE!!');
      });
    },

    handleFileUpload(){
      this.file = this.$refs.file.files[0];
    },

    erraseImage(id){
      const imageUrl = "/images/" + id;
      const imageEl = document.getElementById('currentImage');

      axios.delete(imageUrl, { responseType:"blob" }).then(function (response) {
        
        const reader = new window.FileReader();
        reader.readAsDataURL(response.data); 
        reader.onload = function() {
        const imageDataUrl = reader.result;
        imageEl.setAttribute('src', imageDataUrl);
        }
      })
    },
    
    next(){      
      if(this.index+1>=this.response.length){
        this.index=0;
        this.updateImage(0);
      }else{
      console.log(this.index);
      this.index++;
      this.updateImage(this.index);
      }      
    },
    prev(){
      if(this.index==0){
        this.index=this.response.length;
      }
      this.index--;
      console.log(this.index);
      this.updateImage(this.index);
    },
  }
}
</script>

